<template>
    <div class="vue-doc-wrapper">
        
        <div class="vue-doc-title">
          <h2 id="vue-doc-定制主题1" class="vue-doc-element">定制主题</h2>
<p class="vue-doc-element">定制新主题，以满足业务和品牌上的多样化视觉需求</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-theme1" class="vue-doc-element">theme</h2>
<p class="vue-doc-element">定制新主题，以满足业务和品牌上的多样化视觉需求</p>

        </div>

        


        <div class="vue-doc-content">
          <p class="vue-doc-element"><code class="vue-doc-element">ivuel</code> 的样式是基于 <code class="vue-doc-element">Less</code> 进行开发的，默认以前缀 <code class="vue-doc-element">.ivu-</code> 作为命名空间，并且定义了一套样式变量，定制主题，就是编辑<code class="vue-doc-element">less</code>变量列表。</p>
<h2 id="vue-doc-普通方法1" class="vue-doc-element">普通方法</h2>
<p class="vue-doc-element">如果你的项目是全部引进ivuel样式的,可以通过变量覆盖的方式来实现主题定制。
新建一个less文件引入</p>
<pre class="vue-doc-element"><code class="lang-js"><span class="hljs-keyword">@import</span> <span class="hljs-string">'~ivuel/src/styles/index.less'</span>;

<span class="hljs-comment">// Here are the variables to cover, such as:</span>
<span class="hljs-variable">@primary-color:</span> <span class="hljs-number">#8c0776</span>;
</code></pre>
<p class="vue-doc-element">完整的变量列表可以查看 <a class="vue-doc-element" href="https://github.com/yuhonyon/ivuel/blob/master/src/styles/custom.less">默认样式变量</a>。</p>
<p class="vue-doc-element">然后在入口文件 main.js 内导入这个 less 文件即可：</p>
<pre class="vue-doc-element"><code class="lang-js"><span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>;
<span class="hljs-keyword">import</span> ivuel <span class="hljs-keyword">from</span> <span class="hljs-string">'ivuel'</span>;
<span class="hljs-keyword">import</span> <span class="hljs-string">'./styles/new-ivuel.less'</span>;

Vue.use(ivuel);
</code></pre>
<h2 id="vue-doc-按需引进2" class="vue-doc-element">按需引进</h2>
<p class="vue-doc-element">如果你使用了按需引进样式的方式,可以通过<code class="vue-doc-element">less-loader</code>的<code class="vue-doc-element">modifyVars</code>配置来实现主题定制
通过package.json下的theme字段配置变量,也可以独立一个theme.json文件配置</p>
<pre class="vue-doc-element"><code class="lang-json"><span class="hljs-comment">//package.json</span>
{
  <span class="hljs-string">"name"</span>: <span class="hljs-string">"vue-project"</span>,
  <span class="hljs-string">"version"</span>: <span class="hljs-string">"1.0.0"</span>,
  ...
  <span class="hljs-string">"theme"</span>:{<span class="hljs-comment">//此处修改样式变量</span>
    <span class="hljs-string">"@primary-color"</span>:<span class="hljs-string">"red"</span>
  }
}

</code></pre>
<p class="vue-doc-element">配置<code class="vue-doc-element">modifyVars</code>,下面是vue-cli webpack模板的配置方法</p>
<pre class="vue-doc-element"><code class="lang-js">  <span class="hljs-comment">//vue-cli /build/utils.js</span>
  const packageConfig = require(<span class="hljs-string">'../package.json'</span>)
  <span class="hljs-keyword">return</span> {
    <span class="hljs-string">css :</span> generateLoaders(),
    <span class="hljs-string">postcss :</span> generateLoaders(),
    <span class="hljs-string">less :</span> generateLoaders(<span class="hljs-string">'less'</span>,{<span class="hljs-string">"modifyVars"</span>:packageConfig.theme}),<span class="hljs-comment">//修改此行</span>
    <span class="hljs-string">sass :</span> generateLoaders(<span class="hljs-string">'sass'</span>, { <span class="hljs-string">indentedSyntax:</span> <span class="hljs-literal">true</span> }),
    <span class="hljs-string">scss :</span> generateLoaders(<span class="hljs-string">'sass'</span>),
    <span class="hljs-string">stylus :</span> generateLoaders(<span class="hljs-string">'stylus'</span>),
    <span class="hljs-string">styl :</span> generateLoaders(<span class="hljs-string">'stylus'</span>)
  }
</code></pre>

        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        


    </div>
  </template>
  <script>
      
  
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


